function getImgHtml({ src, dataSource, alt }) {
  return `<img
        class="gallery-image"
        src="${src}"
        data-source="${dataSource}"
        alt="${alt}"
        />`;
}

function addGalleryItems(galleryImages) {
  const createItemsMarkup = galleryImages
    .map(({ preview, original, description }) => {
      return `
      <li class="gallery-item">
      <a class="gallery-link" href="${original}">
          ${getImgHtml({
            src: preview,
            dataSource: original,
            alt: description,
          })}
      </a>
      </li>
        `;
    })
    .join("");

  const galleryContainerEl = document.querySelector(".gallery");
  galleryContainerEl.insertAdjacentHTML("beforeend", createItemsMarkup);
}

(() => {
  addGalleryItems(images);

  const gallery = document.querySelector("ul.gallery");

  new SimpleLightbox(".gallery a", {
    captions: true,
    captionType: "attr",
    captionsData: "alt",
    captionPosition: "bottom",
    captionDelay: 250,
  });

  gallery.addEventListener("click", (event) => {
    event.preventDefault();

    const target = event.target;

    if (target.tagName !== "IMG") {
      return;
    }

    console.log("Big image URL: ", target.getAttribute("data-source"));
  });
})();
